<script>
// imports
import { midPointerMeter } from "../scripts/meter.js"

export default {
  props:[`value`,"settings"],
  data() {
    return {
			meter: "no value", 
    //end return
    };
  // end data
  },
  mounted(){
      this.runMeter() 
  },
  watch: {
      value: function(){
          let oldValue;
          if(this.value != oldValue && this.settings.manMode === false || this.settings.manValue != oldValue && this.settings.manMode === true){
              if(this.settings.manMode === true){
                this.runMeter(); 
                oldValue = this.settings.manValue;
              }
              else{
                this.runMeter();    
                oldValue = this.value; 
              }
          }
      // end watch function
      }
  },
  methods: {
    runMeter(){
        this.meter = midPointerMeter(this.value,this.settings)       
        this.$emit('meter', this.meter);   
        document.getElementById("arrow").style.transform = `rotate(${this.meter.pointerDeg}deg)`;
      }
  },
// end export  
};
</script>
